import React,{Component} from 'react'
import { Button, Form, Header, Segment,Modal } from 'semantic-ui-react'
import 'semantic-ui-css/semantic.css'

class SignupFrom extends Component{
  render(){
    const fixed = this.props.befixed
    return(
    <Modal size='tiny' trigger={<Button as='a' inverted={!fixed} primary={fixed} style={{ marginLeft: '0.5em' }}>
      Sign Up
      </Button>}>
    <Header icon='address card' content='Sign up for an account.' />
    <Modal.Content>
    <Form size='large'>
      <Segment>
        <Form.Input fluid icon='user' iconPosition='left' placeholder='username' />
        <Form.Input fluid icon='mail' iconPosition='left' placeholder='e-mail address' />
        <Form.Input
          fluid
          icon='lock'
          iconPosition='left'
          placeholder='Password'
          type='password'
        />
        <Button color='black' fluid size='large'>
          Sign up
        </Button>
      </Segment>
    </Form>
    </Modal.Content>
    </Modal>
    )
  }
}

export default SignupFrom