import React,{Component} from 'react'
import { Button, Form, Header, Segment,Modal } from 'semantic-ui-react'
import { connect } from 'react-redux'
import {login} from './store/actionCreators'
import 'semantic-ui-css/semantic.css'

class LoginForm extends Component{ 

  render(){
    const fixed = this.props.befixed
    return(
      <Modal size='tiny' trigger={<Button as='a' inverted={!fixed} onMouseOver={()=>console.log(this.props.loginStatus)} onClick={this.props.newOpen} > Log in </Button>}>
      <Header icon='sign-in' content='Log in to your account.' />
      <Modal.Content>
      <Form size='large'>
        <Segment>
          <Form.Input fluid icon='user' iconPosition='left' value={this.props.account} onChange={this.props.handleChange} placeholder='username'  />
          <Form.Input
            fluid
            icon='lock'
            iconPosition='left'
            placeholder='Password'
            type='password'
            value={this.props.password}
            onChange={this.props.handleChange}
          />
          <Button color='black' fluid size='large' onClick={()=>this.props.submit(this.props.account,this.props.password)}>
            Login
          </Button>
        </Segment>
      </Form>
      </Modal.Content>
      </Modal>
    )
  }
} 

const mapStateToProps = (state) => {
  return {
    loginStatus: state.logIn.login,
    account: state.logIn.account,
    password: state.logIn.password
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    submit(account,password){
      dispatch(login(account,password))
    },

    handleChange(e){
      if(e.target.placeholder==='username')
      dispatch({
        type: 'account',
        value: e.target.value
      })
      else
      dispatch({
        type: 'password',
        value: e.target.value
      })
    },

    newOpen(){
      dispatch({
        type: 'refresh'
      })
    }
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(LoginForm);