const defaultState = {
  login: false,
  account:[],
  password:[],
}

export default (state=defaultState, action) => {
  const newState = JSON.parse(JSON.stringify(state));
  switch(action.type) {
      case 'account':
          newState.account = action.value;
          return newState;
      case 'password':
          newState.password = action.value;
          return newState;
      case 'login':
          newState.login = true
          return newState;
      case 'logout':
          newState.login = false
          return newState;
      case 'refresh':
          return defaultState;
      default:
          return state;
  }
}
