import { combineReducers } from 'redux';
import liveDataReducer from '../pages/home/component/liveData/store/reducer';
import logInReducer from '../pages/home/component/logInForm/store/reducer';
import recentBlocksReducer from '../pages/home/component/recentBlocks/store/reducer';

export default combineReducers({
  liveData: liveDataReducer,
  logIn: logInReducer,
  recentBlocks: recentBlocksReducer
});