import React,{Component} from 'react'
import { ItemGroup,Item,Statistic,Segment } from 'semantic-ui-react'
import { connect } from 'react-redux'
import 'semantic-ui-css/semantic.css'
import blockHeight from '../../../../statics/blockHeight.png'
import blockInterval from '../../../../statics/blockInterval.png'
import transactionRate from '../../../../statics/transactionRate.png'
import axios from 'axios'

class LiveData extends Component{

  componentDidMount() {
    this.interval = setInterval(this.props.changeLiveData, 1000);
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  render(){
  return(
  <Segment raised padded>
  <ItemGroup divided unstackable>
  <Item>
    <Item.Image size='tiny' src={blockHeight}/>
    <Item.Content verticalAlign='middle'>
    <Statistic floated='right' size='small'>
      <Statistic.Label>Block  Height</Statistic.Label>
      <Statistic.Value>{this.props.blockHeight}</Statistic.Value>
    </Statistic>
    </Item.Content>
  </Item>
  <Item>
    <Item.Image size='tiny' src={blockInterval}/>
    <Item.Content verticalAlign='middle'>
    <Statistic floated='right' size='small'>
      <Statistic.Label>Block  Interval</Statistic.Label>
      <Statistic.Value>{this.props.blockInterval}s</Statistic.Value>
    </Statistic>
    </Item.Content>
  </Item>
  <Item>
    <Item.Image size='tiny' src={transactionRate}/>
    <Item.Content verticalAlign='middle'>
    <Statistic floated='right' size='small'>
      <Statistic.Label>Transaction  Rate</Statistic.Label>
      <Statistic.Value>{this.props.transactionRate}/s</Statistic.Value>
    </Statistic>
    </Item.Content>
  </Item>
</ItemGroup>
</Segment>
    )
  }
}



const mapStateToProps = (state) => {
  return {
    blockHeight: state.liveData.blockHeight,
    blockInterval: state.liveData.blockInterval,
    transactionRate: state.liveData.transactionRate
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    changeInputValue(){
      const action = {
        type: 'xixi'
      };
      dispatch(action);
    },

    changeLiveData(){
      dispatch((dispatch)=>{
        // axios.get('/api/liveData.json').then((res)=>{
        //   const data= JSON.parse(JSON.stringify(res.data));
        //   data.blockHeight+=(Math.floor(Math.random() * (20 - 10) + 10));
        //   data.blockInterval+=(Math.floor(Math.random() * (20 - 10) + 10));
        //   data.transactionRate+=(Math.floor(Math.random() * (20 - 10) + 10));
        //   const action={
        //     type: 'changeLiveData',
        //     blockHeight: data.blockHeight,
        //     blockInterval: data.blockInterval,
        //     transactionRate: data.transactionRate
        //   };
        //   dispatch(action);
        // }).catch(()=>{console.log('error')})
        const data={
          "blockHeight": 234,
          "blockInterval":  17.3,
          "transactionRate": 79
        };
          data.blockHeight+=(Math.floor(Math.random() * (20 - 10) + 10));
          data.blockInterval+=(Math.floor(Math.random() * (20 - 10) + 10));
          data.transactionRate+=(Math.floor(Math.random() * (20 - 10) + 10));
        const action={
          type: 'changeLiveData',
          blockHeight: data.blockHeight,
          blockInterval: data.blockInterval,
          transactionRate: data.transactionRate
        };
        dispatch(action);
      });
    }
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(LiveData);