import PropTypes from 'prop-types'
import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import {
  Button,
  Container,
  Divider,
  Grid,
  Header,
  Icon,
  List,
  Menu,
  Responsive,
  Segment,
  Sidebar,
  Visibility,
  Input,
  Modal,
} from 'semantic-ui-react'
import 'semantic-ui-css/semantic.css'
import LoginForm from './component/logInForm'
import SignupFrom from './component/signUpForm'
import LiveData from './component/liveData'
import RecentBlocks from './component/recentBlocks'
import RecentTrans from './component/recentTrans'
import Candlestick from '../.././component/candleStick'
import backPic from '../../statics/background.jpg'
import Account from '../.././component/account'


// Heads up!
// We using React Static to prerender our docs with server side rendering, this is a quite simple solution.
// For more advanced usage please check Responsive docs under the "Usage" section.
const getWidth = () => {
  const isSSR = typeof window === 'undefined'

  return isSSR ? Responsive.onlyTablet.minWidth : window.innerWidth
}

/* eslint-disable react/no-multi-comp */
/* Heads up! HomepageHeading uses inline styling, however it's not the best practice. Use CSS or styled components for
 * such things.
 */
const HomepageHeading = ({ mobile }) => (
  <Container text textAlign={mobile? 'center' : 'left'}>
    <Header
      as='h1'
      content='PowExChain'
      inverted
      style={{
        fontSize: mobile ? '2.2em' : '5em',
        fontWeight: 'normal',
        fontFamily: '"Trebuchet MS", Helvetica, sans-serif',
        marginBottom: 0,
        marginTop: mobile ? '3em' : '3.3em',
      }}
    />
    <Header
      as='h2'
      content='Power the Future.'
      inverted
      style={{
        fontSize: mobile ? '1.2em' : '2.5em',
        fontWeight: 'normal',
        fontFamily: '"Trebuchet MS", Helvetica, sans-serif',
        marginTop: mobile ? '0.5em' : '1.5em',
      }}
    />
  </Container>
)

HomepageHeading.propTypes = {
  mobile: PropTypes.bool,
}


class Desktop extends Component {
  state = {}
  hideFixedMenu = () => this.setState({ fixed: false })
  showFixedMenu = () => this.setState({ fixed: true })

  handleScrollTop=()=>{
    let scrollToTop = window.setInterval(function() {
      let pos = window.pageYOffset;
      if ( pos > 0 ) {
          window.scrollTo( 0, pos - 20 ); // how far to scroll on each step
      } else {
          window.clearInterval( scrollToTop );
      }
    }, 1);
  }

  render() {
    const { children } = this.props
    const { fixed } = this.state

    return (
      <Responsive getWidth={getWidth} minWidth={Responsive.onlyTablet.minWidth}>
        <Visibility
          once={false}
          onBottomPassed={this.showFixedMenu}
          onBottomPassedReverse={this.hideFixedMenu}
        >
          <Segment
            inverted
            textAlign='center'
            style={{ minHeight: 700, padding: '1em 0em', backgroundImage: 'url(' + backPic + ')', backgroundSize: 'cover', backgroundRepeat: 'no-repeat' }}
            vertical
          >
            <Menu
              fixed={fixed ? 'top' : null}
              inverted={!fixed}
              pointing={!fixed}
              secondary={!fixed}
              size='large'
            >
              <Container>
                <Link to='/' ><Menu.Item name='Home' active /></Link>
                <Link to='/exchange'><Menu.Item name='View Exchange' /></Link>
                <Menu.Item position='right' >
                  <Input style={{ width: 300 + 'px' }} icon='search' placeholder='Search by Address / Txhash / Block ...' />
                </Menu.Item>
                {!this.props.loginStatus ? 
                  <Menu.Item position='right'>
                    <LoginForm befixed={fixed} />
                    <SignupFrom befixed={fixed} />
                  </Menu.Item> : <Menu.Item position='right'>
                    <Account befixed={fixed} />
                    <Button as='a' inverted={!fixed} primary={fixed} style={{ marginLeft: '0.5em' }} onClick={this.props.logout}>Log Out </Button>
                  </Menu.Item>}
              </Container>
            </Menu>
            <HomepageHeading />
          </Segment>
        </Visibility>
        {children}
        <Button  icon='angle up' size='huge' circular onClick={this.handleScrollTop} style={{position: 'fixed',right: '3em',bottom: '4em',display: fixed?'block':'none'}} />
      </Responsive>
    );
  }
}

Desktop.propTypes = {
  children: PropTypes.node,
}

class Mobile extends Component {
  state = {}

  handleSidebarHide = () => this.setState({ sidebarOpened: false })

  handleToggle = () => this.setState({ sidebarOpened: true })

  render() {
    const { children } = this.props
    const { sidebarOpened } = this.state
    return (
      <Responsive
        as={Sidebar.Pushable}
        getWidth={getWidth}
        maxWidth={Responsive.onlyMobile.maxWidth}
      >
        <Sidebar
          as={Menu}
          animation='push'
          inverted
          onHide={this.handleSidebarHide}
          vertical
          visible={sidebarOpened}
        >
          <Link to='/' ><Menu.Item name='Home' active /></Link>
          <Link to='/exchange'><Menu.Item name='View Exchange' /></Link>
          <Menu.Item >
          <Input icon='search' placeholder='Search by Address / Txhash / Block ...' />
          </Menu.Item>
        </Sidebar>

        <Sidebar.Pusher dimmed={sidebarOpened}>
          <Segment
            inverted
            textAlign='center'
            style={{ minHeight: 350, padding: '1em 0em',backgroundImage: 'url('+backPic+')',backgroundSize: 'cover',backgroundRepeat: 'no-repeat' }}
            vertical
          >
            <Container>
              <Menu inverted pointing secondary size='large'>
                <Menu.Item onClick={this.handleToggle}>
                  <Icon name='sidebar' />
                </Menu.Item>
                {!this.props.loginStatus ?
                  <Menu.Item position='right'>
                    <LoginForm />
                    <SignupFrom />
                  </Menu.Item> : <Menu.Item position='right'>
                    <Account />
                    <Button as='a' inverted style={{ marginLeft: '0.5em' }} onClick={this.props.logout}>Log Out </Button>
                  </Menu.Item>}
              </Menu>
            </Container>
            <HomepageHeading mobile />
          </Segment>
          {children}
        </Sidebar.Pusher>
      </Responsive>
    );
  }
}

Mobile.propTypes = {
  children: PropTypes.node,
}

const ResponsiveContainer = ({ children }) => (
  <div>
    <DesktopContainer>{children}</DesktopContainer>
    <MobileContainer>{children}</MobileContainer>
  </div>
)

ResponsiveContainer.propTypes = {
  children: PropTypes.node,
}

const HomepageLayout = () => (
  <ResponsiveContainer>
    <Segment style={{ padding: '6em 0em' }} vertical>
      <Grid container stackable verticalAlign='middle'>
        <Grid.Row divided>
          <Grid.Column width={10}>
            <Candlestick />
          </Grid.Column>
          <Grid.Column floated='right' width={6}>
            <LiveData />
          </Grid.Column>
        </Grid.Row>
        <Grid.Row>
          <Grid.Column textAlign='center'>
            <Link to='/exchange'>
            <Button secondary size='huge' content='Start Trading' style={{marginTop: '1em'}} />
            </Link>
          </Grid.Column>
        </Grid.Row>
      </Grid>
    </Segment>
    <Segment style={{ padding: '0em' }} vertical>
      <Grid celled='internally' columns='equal' stackable>
        <Grid.Row textAlign='center'>
          <Grid.Column style={{ paddingBottom: '5em', paddingTop: '5em',backgroundColor: 'rgb(27,28,29)'}}>
          <Header as='h3' style={{ fontSize: '2em',color: 'white',fontFamily: '"Trebuchet MS", Helvetica, sans-serif'}}>
            The Most Powerful Decentralized Digital Asset Exchange.
          </Header>
            <p style={{ fontSize: '1.33em',color: 'white', fontFamily:'"Trebuchet MS", Helvetica, sans-serif'}}>
            More than electricity, more deals are coming soon.</p>
          </Grid.Column>
        </Grid.Row>
        <Grid.Row>
          <Grid.Column style={{ paddingBottom: '5em', paddingTop: '5em' }}>
            <RecentBlocks />
          </Grid.Column>
          <Grid.Column style={{ paddingBottom: '5em', paddingTop: '5em' }}>
            <RecentTrans />
          </Grid.Column>
        </Grid.Row>
      </Grid>
    </Segment>
    <Segment inverted vertical style={{ padding: '5em 0em' }}>
      <Container>
        <Grid divided inverted stackable>
          <Grid.Row>
            <Grid.Column width={3}>
              <Header inverted as='h4' content='About' />
              <List link inverted>
                <List.Item as='a'>Sitemap</List.Item>
                <List.Item as='a'>Contact Us</List.Item>
                <List.Item as='a'>Religious Ceremonies</List.Item>
                <List.Item as='a'>Gazebo Plans</List.Item>
              </List>
            </Grid.Column>
            <Grid.Column width={3}>
              <Header inverted as='h4' content='Services' />
              <List link inverted>
                <List.Item as='a'>Banana Pre-Order</List.Item>
                <List.Item as='a'>DNA FAQ</List.Item>
                <List.Item as='a'>How To Access</List.Item>
                <List.Item as='a'>Favorite X-Men</List.Item>
              </List>
            </Grid.Column>
            <Grid.Column width={7}>
              <Header as='h4' inverted>
                Footer Header
              </Header>
              <p>
                Extra space for a call to action inside the footer that could help re-engage users.
              </p>
            </Grid.Column>
          </Grid.Row>
        </Grid>
      </Container>
    </Segment>
  </ResponsiveContainer>
)

const mapStateToProps = (state) => {
  return {
    loginStatus: state.logIn.login
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    logout(){
      dispatch({
        type: 'logout'
      })
    },


  }
}

const DesktopContainer = connect(mapStateToProps,mapDispatchToProps)(Desktop);
const MobileContainer = connect(mapStateToProps,mapDispatchToProps)(Mobile);

export default connect(null,null)(HomepageLayout);