import React from 'react'
import { Form, Input, TextArea, Button, Select, Segment } from 'semantic-ui-react'
import 'semantic-ui-css/semantic.css'

const transOptions = [
  { key:'b',text:'Buy',value:'buy'},
  { key:'s',text:'Sell',value:'Sell'}
]

const typeOptions = [
  { key: 'f', text: 'Wind', value: 'wind' },
  { key: 's', text: 'Solar', value: 'solar' },
  { key: 'w', text: 'Water', value: 'water' },
]

const Buy = () => (
  <Segment inverted >
  <Form inverted style={{padding: '0.5em 0'}}>
      <Form.Field
        control={Select}
        options={transOptions}
        label={{ children: 'Buy/Sell', htmlFor: 'form-select-control-buy-sell' }}
        placeholder='Buy/Sell'
      />
      <Form.Field
        id='form-input-control-value'
        control={Input}
        label='Value(kWh)'
        placeholder='Value(kWh)'
      />
      <Form.Field
        id='form-input-control-price'
        control={Input}
        label='Price()'
        placeholder='Price()'
      />
      <Form.Field
        control={Select}
        options={typeOptions}
        label={{ children: 'Type', htmlFor: 'form-select-control-type' }}
        placeholder='Type'
      />
      <Form.Checkbox label='Are you SURE of this transaction?' required/>
      <Button negative type='submit' >Submit</Button>
  </Form>
  </Segment>
)

export default Buy