const defaultState = {
  id: [],
  hash: [],
  transNumber: []
}

export default (state=defaultState, action) => {
  const newState = JSON.parse(JSON.stringify(state));
  switch(action.type) {
      case 'changeRecentBlocks':
          newState.id = action.id;
          newState.hash = action.hash;
          newState.transNumber = action.transNumber;
          return newState;
      default:
          return state;
  }
}