const defaultState={
  blockHeight: 0,
  blockInterval: '0.0 ',
  transactionRate: 0
}

export default (state=defaultState,action)=>{
  switch (action.type) {
    case 'changeLiveData':
    return {
      blockHeight: action.blockHeight,
      blockInterval: action.blockInterval,
      transactionRate: action.transactionRate
    };
    default:
    return state;
  }
}