import axios from 'axios'

export const login = (account, password) => {
  return (dispatch) => {
    axios.get('/api/login.json?account=' + account + '&password=' + password).then((res) => {
        if(res.data.success)
        dispatch({
          type: 'login'
        })
        else
        alert('False')
    })
  }
}