import React,{Component} from 'react'
import { Button, Form, Grid, Header, Image, Message, Segment,Modal } from 'semantic-ui-react'
import 'semantic-ui-css/semantic.css'

class Account extends Component{
  render(){
    const fixed = this.props.befixed
    return(
      <Modal size='large' trigger={<Button as='a' circular icon='user' inverted={!fixed} style={{marginRight: '1em'}}></Button>}>
      <Header icon='columns' content='Your Account' />
      <Modal.Content>
        <Segment></Segment>
        <Segment></Segment>
        <Segment></Segment>
      </Modal.Content>
      <Modal.Actions>
        
      </Modal.Actions>
      </Modal>
    )
  }
}

export default Account